@use "../../../shared.scss";

.qa-tool-checkbox__input {
    position: absolute;
    z-index: -1;
    opacity: 0;

    & + label {
        display: inline-flex;
        align-items: center;
        user-select: none;

        &::before {
            content: "";
            display: inline-block;
            width: 1em;
            height: 1em;
            flex-shrink: 0;
            flex-grow: 0;
            border: 1px solid var(--gray-border-color);
            border-radius: 0.25em;
            margin-right: 5px;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 70% 70%;
        }
    }

    &:not(:disabled) {
        & + label {
            cursor: pointer;
        }

        &:not(:checked) + label:hover::before {
            border-color: var(--green-btn-bkg-color);
        }

        &:active + label::before {
            background-color: var(--green-btn-bkg-color-half-transparent);
            border-color: var(--green-btn-bkg-color-half-transparent);
        }
    }

    &:disabled + label::before {
        background-color: var(--green-bkg-color);
    }

    &:checked + label::before {
        border-color: var(--green-btn-bkg-color);
        background-color: var(--green-btn-bkg-color);
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    }

    &:focus {
        & + label::before {
            box-shadow: 0 0 0 0.2rem var(--green-btn-bkg-color-half-transparent);
        }

        &:not(:checked) + label::before {
            border-color: var(--green-btn-bkg-color);
        }
    }
}
