.qa-item-view-mode {
    &__question-btns {
        display: flex;
    }

    :hover .qa-tool__buttons-group {
        visibility: visible !important;
    }

    &__question {
        display: flex;
        flex-grow: 1;
        cursor: pointer;
        word-break: break-word;

        i::before {
            font-size: small;
            transition-duration: 0.5s;
            transition-property: transform;
            vertical-align: baseline;
        }

        h5 {
            display: inline;
            margin-left: 5px;
        }
    }

    &__answer {
        height: 0px;
        opacity: 0;
        white-space: pre-wrap;
        word-break: break-word;
        transform: translateY(-1.25em);
        transition-duration: 0.2s;
        transition-property: transform;
        overflow: hidden;

        &--empty {
            color: #aaa;
            font-style: italic;
        }
    }

    &--open {
        .qa-item-view-mode__question i::before {
            transform: rotate(90deg);
        }

        .qa-item-view-mode__answer {
            display: block;
            height: auto;
            opacity: 100;
            margin: 5px 0 0 20px;
            transform: translateY(0);
        }
    }
}

.no-touch .qa-item-view-mode .qa-tool__buttons-group {
    visibility: hidden;
}
