.qa-tool-content {
    display: flex;
    overflow: hidden;

    &__inner-container {
        display: flex;
        width: 100%;

        @media only screen and (max-width: 1024px) {
            flex-direction: column;
        }
    }

    &__info {
        border-style: solid;
        border-width: 0 1px 0 0;
        border-color: gray;
        margin: 0 10px 5px 0;
        padding: 0 10px 0 0;
        max-width: 300px;

        @media only screen and (max-width: 1024px) {
            border-width: 0 0 1px 0;
            margin: 0 0 10px 0;
            padding: 0 0 10px 0;
            max-width: 100%;
        }
    }

    .qa-manager {
        width: 100%;
    }

    h3 {
        @media only screen and (max-width: 400px) {
            font-size: 1.3rem;
        }
        @media only screen and (max-width: 370px) {
            font-size: 1.2rem;
        }
    }

    h5 {
        @media only screen and (max-width: 370px) {
            font-size: 1rem;
        }
    }
}
