@use "../../shared.scss";

.qa-item-edit-mode {
    &__buttons {
        > button {
            width: 42px;
            height: 38px;
        }

        img {
            margin-left: -4px;
            margin-top: -3px;
        }
    }

    &__title {
        display: flex;
        word-break: break-word;
        h3 {
            color: var(--section-title-color);
        }
        h5 {
            color: var(--section-title-color);
        }
    }

    &__question-label {
        display: block;
    }

    &__question--invalid {
        border-color: var(--red-border-color);
        background-color: var(--red-bkg-color);
    }

    &__btns-checkbox {
        display: flex;
        flex-direction: column;
        margin-left: auto;

        .qa-tool-checkbox__label {
            font-size: small;
        }
    }
}
