@use "./shared.scss";

.qa-tool {
    padding: 20px 15px 15px 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-width: 250px;

    h1 {
        margin-bottom: 20px;
        text-align: center;
    }

    &__buttons-group {
        white-space: nowrap;
    }

    &__buttons-group > button:not(:first-child) {
        margin-left: 5px;
    }

    @media only screen and (max-width: 800px) {
        padding: 10px 5px 5px 10px;
    }
}

.loading-indicator {
    width: 24px;
    height: auto;
}

.qa-tool-confirm-alert {
    width: 500px;
    border: 1px solid --var(--gray-border-color);
    border-radius: --var(--border-radius);
    padding: 30px;
    background-color: var(--gray-bkg-color);

    h5 {
        text-align: center;
        @media only screen and (max-width: 500px) {
            font-size: 1rem;
        }
    }

    &__btns {
        display: flex;
        justify-content: center;
    }

    @media only screen and (max-width: 500px) {
        width: 300px;
    }
}

.no-touch {
    .qa-tool {
        max-height: 100vh;
    }
}

.info-popup {
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: -100px;
    transition-duration: 0.5s;
    transition-property: bottom;

    > div {
        background-color: var(--green-bkg-color);
        color: var(--green-text-color);
        border: 1px solid var(--green-border-color);
        border-radius: var(--border-radius);
        border-radius: var(--border-radius);
        padding: 10px;
    }

    &--open {
        bottom: 30px;
    }
}
